.arrows-alternative .glide .glide__arrows .glide__arrow {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 50px;
  height: 50px;
  padding: 0;
  font-family: "icons";
  background-color: rgba(255, 255, 255, 0.8);
  transition: all var(--t-medium);
  border: none;
  cursor: pointer;
  font-size: 0;
}
.arrows-alternative .glide .glide__arrows .glide__arrow:before {
  height: 100%;
  font-size: 1.8rem;
  line-height: 1.75;
  color: var(--c-primary);
}
.arrows-alternative .glide .glide__arrows .glide__arrow:hover, .arrows-alternative .glide .glide__arrows .glide__arrow:focus {
  background-color: var(--c-white);
}
.arrows-alternative .glide .glide__arrows .glide__arrow:hover:before, .arrows-alternative .glide .glide__arrows .glide__arrow:focus:before {
  color: var(--c-primary);
}
.arrows-alternative .glide .glide__arrows .glide__arrow.glide__arrow--left:before {
  content: "\e804";
}
.arrows-alternative .glide .glide__arrows .glide__arrow.glide__arrow--right:before {
  content: "\e800";
}

.pagination-default .glide {
  list-style: none;
}
.pagination-default .glide .glide__bullets {
  display: flex;
  justify-content: center;
  gap: 0.5em;
  position: absolute;
  inset: auto auto 2em auto;
  width: 100%;
  transition: 300ms opacity;
  transform: translate3d(0, 0, 0);
  z-index: 100;
}
.pagination-default .glide .glide__bullets .glide__bullet {
  background-color: transparent;
  opacity: 1;
  width: 1.4em;
  height: 1.4em;
  margin: 0 0.1em;
  padding: 0;
  border-radius: 50%;
  border: 2px solid var(--c-white);
  box-shadow: 0 0.25em 0.5em 0 rgba(0, 0, 0, 0.1);
  transition: all 0.3s ease-in-out;
  line-height: 0;
  cursor: pointer;
}
.pagination-default .glide .glide__bullets .glide__bullet.glide__bullet--active {
  background-color: var(--c-white);
}

.overlay .glide .glide__track:after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: block;
  background-color: rgba(0, 0, 0, 0.3);
  z-index: 102;
}

.transition-fade .glide .glide__slides {
  transform: none !important;
  width: auto !important;
  display: block;
}
.transition-fade .glide .glide__slide {
  position: absolute;
  left: 0;
  top: 0;
  opacity: 0;
  transition: opacity 1s;
}
.transition-fade .glide .glide__slide:first-child {
  position: relative;
}
.transition-fade .glide .glide__slide--active {
  z-index: 1;
  opacity: 1;
}

.masthead {
  position: relative;
  background-color: var(--c-black);
  z-index: 8000;
}
@media (max-width: 1120px) {
  .masthead {
    margin-top: var(--header-height);
  }
}
.masthead .masthead__slideshow .glide .glide__slide picture,
.masthead .masthead__slideshow .glide .glide__slide img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
@media (max-width: 480px) {
  .masthead .masthead__slideshow .glide .glide__arrows {
    display: none;
  }
}
.masthead .masthead__slideshow.interior .glide {
  max-width: 1920px;
  max-height: 700px;
  overflow: hidden;
  margin-inline: auto;
}

.masthead--video {
  position: relative;
}
.masthead--video video {
  display: block;
  width: 100%;
  height: 100vh;
  object-fit: cover;
}
@media (max-width: 1120px) {
  .masthead--video video {
    height: 50vh;
    margin-top: var(--header-height);
  }
}
@media (max-width: 1120px) {
  .masthead--video .content-overlay {
    display: none;
  }
}

#skip-nav {
  position: absolute;
  top: 0;
  width: 300px;
}
#skip-nav a:link,
#skip-nav a:visited {
  background-color: #fff;
  color: #000;
  font-size: 20px;
  font-weight: 400;
  overflow: hidden;
  padding: 10px;
  position: absolute;
  left: -9999px;
  text-decoration: none;
  width: 1px;
  height: 1px;
}
#skip-nav a:focus {
  left: 0;
  width: auto;
  height: auto;
  outline: 1px solid #000;
  overflow: visible;
  text-decoration: none;
  z-index: 20000;
}
#skip-nav a:hover {
  background-color: #000;
  color: #fff;
}

.site-map {
